.flip-box {
  background-color: transparent;
  width: 215px;
  height: 200px;
  border: 1px solid transparent;
  perspective: 1000px;
  padding-right: 15px;
}

.flip-box-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-box:hover .flip-box-inner {
  transform: rotateY(180deg);
}

.flip-box-front,
.flip-box-backOdd,
.flip-box-backEven {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.flip-box-front {
  background-color: rgb(238, 237, 237);
  color: black;
}

.flip-box-backOdd,
.flip-box-backEven {
  color: white;
  transform: rotateY(180deg);
}

.flip-box-backOdd {
  background-color: #4570e0;
}

.flip-box-backEven {
  background-color: #e91e63;
}

.flip-box-front .FrontImage {
  width: 100%;
  height: 100%;
  padding: 10px;
}
